<template>
  <div class="panoramicAnalysis">
    <div class="top">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>行为热点分布</span>
            </div>
          </div>
          <div class="chart-main">
            <pieCharts
              v-if="behaviorRes"
              :pieChartsData="behaviorData"
              :roseType="false"
              :radius="['40%', '80%']"
              :name="'行为热点分布'"
              @setOption="setOption"
            ></pieCharts>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <div class="content-title-left">
              <span class="block"></span>
              <span>工单热点分布</span>
            </div>
          </div>
          <div class="chart-main" id="randomCircle">
            <wordCloud
              v-if="hotWordRes"
              :wordCloudData="hotWordData"
              :name="'工单热点分布'"
              @setOption="setOption"
            ></wordCloud>
          </div>
        </div>
      </div>
    </div>

    <div class="bottom content-border">
      <div class="content">
        <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span class="text">客户关注热点趋势分析</span>
            <div class="change-type">
              <span
                class="change-button"
                :class="customerFocusActive==='1'?'active':''"
                @click="changeCustomerFocus('1')"
              >申请报装</span>
              <span
                class="change-button"
                :class="customerFocusActive==='2'?'active':''"
                @click="changeCustomerFocus('2')"
              >微信注册</span>
              <span
                class="change-button"
                :class="customerFocusActive==='3'?'active':''"
                @click="changeCustomerFocus('3')"
              >浏览推文</span>
            </div>
          </div>
          <div class="content-title-right">
            <el-date-picker
              v-model="customerFocusTime"
              type="date"
              size="mini"
              value-format="yyyy-MM-dd"
              placeholder="选择时间"
              @change="getCustomerFocusData"
            ></el-date-picker>
          </div>
        </div>
        <div class="chart-main">
          <barCharts
            v-if="customerFocusRes"
            :barChartsData="customerFocusData"
            :barWidth="40"
            :barType="'bar'"
            :isShadow="false"
            :name="'客户关注热点趋势分析'"
            @setOption="setOption"
            :maxShowNumber="13"
          ></barCharts>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Interface from '@/base/mixin/user-behavior';
import barCharts from '@/components/bar-charts/bar-charts';
import pieCharts from '@/components/pie-charts/pie-charts'
import wordCloud from '@/components/word-cloud/word-cloud'

export default {
  components: {
    barCharts,
    pieCharts,
    wordCloud,
  },
  name: "panoramicAnalysis",
  // mixins: [Interface],
  data() {
    return {
      //行为热点分布
      behaviorData: {},
      behaviorRes: false,
      //工单热点分布
      hotWordData: {},
      hotWordRes: false,
      //客户关注热点趋势分析
      customerFocusActive: "1",
      customerFocusTime: '',
      customerFocusData: [],
      customerFocusRes: false,
      //图表组件数据
      setOptionData: {},
    }
  },
  mounted() {
    this.getBehaviorData()
    this.getHotWordData()
    this.getCustomerFocusData()
    window.addEventListener("resize", () => {
      if (this.setOptionData['行为热点分布']) this.setOptionData['行为热点分布'].resize()
      if (this.setOptionData['工单热点分布']) this.setOptionData['工单热点分布'].resize()
      if (this.setOptionData['客户关注热点趋势分析']) this.setOptionData['客户关注热点趋势分析'].resize()
    });
  },
  methods: {
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val
    },
    //改变客户关注类型
    changeCustomerFocus(val) {
      if (val) this.customerFocusActive = val;
      this.getCustomerFocusData()
    },
    // 获取IVR服务类型占比分布
    getBehaviorData() {
      this.behaviorRes = false
      // this.proportionDistributionBehaviors().then(res => {
      //   this.behaviorData = {
      //     dataMap: res.map(e => {
      //       return {
      //         name: e.ivrfwlx,
      //         value: e.zb,
      //       }
      //     }),
      //     colorMap: [
      //       '#1A84F4',
      //       '#08BEF3',
      //       '#1DE0BF',
      //       '#EAA01A',
      //     ],
      //   }
      // this.behaviorRes = true
      // })
      this.behaviorData = {
        dataMap: [
          { name: "微信注册", value: "150" },
          { name: "浏览推文", value: "100" },
          { name: "申请报装", value: "500" },
        ],
        colorMap: [
          '#1A84F4',
          '#EAA01A',
          '#1DE0BF',
        ],
      }
      this.behaviorRes = true
    },
    //获取热词
    getHotWordData() {
      this.hotWordRes = false
      // this.hotWordDistribution().then(res => {
      //   console.log(res, 'hotWordDistribution')
      //   if (res[1]) {
      //     this.hotWordData = res[1].map(e => {
      //       return {
      //         name: e.rc,
      //         valueShow: e.rcsl,
      //       }
      //     })
      //     this.hotWordRes = true
      //   }
      // })
      this.hotWordData = [
        { name: "电费", valueShow: "3010" },
        { name: "停电", valueShow: "21" },
        { name: "业扩", valueShow: "122" },
        { name: "电压质量", valueShow: "31" },
        { name: "计量", valueShow: "64" },
      ]
      this.hotWordRes = true

    },
    //智能IVR总体情况分布
    getGeneralSituationData() {
      this.generalSituationRes = false

      this.generalSituation({ selectTime: this.generalSituationTime }).then(res => {
        console.log(res, 'generalSituation')
        let dataMap1 = []
        let dataMap2 = []
        let xNameData = []
        res['znivrl'].forEach(e => {
          dataMap1.push(e.count)
          xNameData.push(e.time)
        })
        res['hwl'].forEach(e => {
          dataMap2.push(e.count)
        })
        this.generalSituationData = {
          dataMap: {
            '智能IVR量': dataMap1,
            '话务量': dataMap2,
          },
          xNameData: xNameData,
          colorMap: {
            '智能IVR量': [
              // '#55BFFF', '#2790FF'
              ['#2790FF', '#55BFFF', '#ddf2ff'],
            ],
            '话务量': [
              // '#55BFFF', '#2790FF'
              ['#E69622', '#F8CC67', '#fdf3df'],
            ],
          },
          xyName: { 'x轴': { name: '', color: "#808080", diffcolor: false }, 'y轴': [{ name: '智能IVR量', color: '#808080' }, { name: '话务量', color: '#808080' }] }
        }
        this.generalSituationRes = true
      })

    },
    // 客户关注热点趋势分析
    getCustomerFocusData() {
      this.customerFocusRes = false

      // this.customerFocusationCalls({ selectTime: this.customerFocusTime }).then(res => {
      // let dataMap = []
      // let xNameData = []
      // res.forEach(e => {
      //   dataMap.push(e.count)
      //   xNameData.push(e.time.split('-')[1] + '-' + e.time.split('-')[2])
      // });
      this.$nextTick(() => {
        let index = parseInt(this.customerFocusActive) - 1
        let dataMap = [
          [
            90,
            80,
            70,
            80,
            70,
            80,
            200,
          ],
          [
            90,
            80,
            70,
            200,
            80,
            70,
            80,
          ],
          [
            90,
            80,
            200,
            70,
            80,
            70,
            80,
          ],
        ]
        let colorMap = []
        dataMap[index].forEach(e => {
          if (e === 200) {
            colorMap.push(["#CC8A00", "#FACE8A"])
          } else {
            colorMap.push(['#2790FF', '#55BFFF'])
          }
        })
        this.customerFocusData = {
          dataMap: {
            '客户关注热点趋势分析': dataMap[index]
          },
          xNameData: [
            '周一',
            '周二',
            '周三',
            '周四',
            '周五',
            '周六',
            '周日',
          ],
          colorMap: {
            '客户关注热点趋势分析': colorMap
          },
          xyName: { 'x轴': { name: '时间', color: "#4D4D4D", diffcolor: false }, 'y轴': [{ name: '调用次数', color: '#808080' }] }
        }
        this.customerFocusRes = true
      })
      // })

    },
  },
}
</script>

<style scoped lang="scss">
::v-deep .el-table .warning-row {
  // background: #e7f6f7;
  background: #e9f3fe;
}
.panoramicAnalysis {
  width: 100%;
  height: 100%;
  font-size: 14px;
  box-sizing: border-box;
  .content-border {
    background: #fff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }
  .top,
  .bottom {
    width: 100%;
    // height: calc(50% - 10px);
    box-sizing: border-box;
    margin-bottom: 20px;
    height: 300px;
    .content {
      width: 100%;
      height: calc(100% - 40px);
      display: flex;
      flex-wrap: wrap;
      .content-title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 30px;
        line-height: 30px;
        padding: 5px 0px 0px 5px;
        font-weight: bold;
        color: #1a84f4;
        .block {
          display: inline-block;
          width: 8px;
          height: 16px;
          margin: 0 10px;
          background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
        }
        .content-title-right {
          margin-right: 20px;
        }
      }
      .chart-main {
        width: 100%;
        height: calc(100% - 30px);
      }
      .content-left {
        width: calc(50% - 10px);
        margin-right: 10px;
        height: 300px;
      }
      .content-right {
        width: calc(50% - 10px);
        height: 300px;
      }
      .content-bottom {
        width: 100%;
        height: 300px;
        margin-top: 20px;
      }
    }
  }
  .bottom {
    .content-title-left {
      display: flex;
      align-items: center;
      .text {
        width: 210px;
        margin-right: 20px;
      }
      .change-type {
        width: 100%;
        height: 28px;
        display: flex;
        align-items: center;
        .active {
          &.change-button {
            color: #fff;
          }

          background-color: #1899fa;
        }
        .change-button {
          width: 100px;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          border: 1px solid #1899fa;
          color: #1899fa;
          cursor: pointer;
          &:nth-child(1) {
            border-right: 0;
            border-radius: 4px 0 0 4px;
          }
          &:nth-child(2) {
            border-radius: 0;
          }
          &:nth-child(3) {
            border-left: 0;
            border-radius: 0 4px 4px 0;
          }
        }
      }
    }
  }
}
</style>